import {
  Component,
  Input,
  ElementRef,
  Renderer2,
  OnInit,
  OnChanges,
  TemplateRef,
} from '@angular/core';
import { updateHostClass, InputNumber } from '@delon/util';
import { SVConfig } from './view.config';

const prefixCls = `sv`;

@Component({
  selector: 'sv-container, [sv-container]',
  templateUrl: './view-container.component.html',
  preserveWhitespaces: false,
})
export class SVContainerComponent implements OnInit, OnChanges {
  private el: HTMLElement;
  //#region fields

  _title = '';
  _titleTpl: TemplateRef<any>;
  @Input()
  set title(value: string | TemplateRef<any>) {
    if (value instanceof TemplateRef) {
      this._title = null;
      this._titleTpl = value;
    } else {
      this._title = value;
    }
  }

  @Input()
  size: 'small' | 'large';

  /** åè¡¨é¡¹é´è·ï¼åä½ä¸º `px` */
  @Input()
  @InputNumber()
  gutter: number;

  @Input()
  layout: 'horizontal' | 'vertical';

  @Input()
  @InputNumber(null)
  labelWidth: number;

  /** æå®ä¿¡æ¯æå¤åå åå±ç¤ºï¼æç»ä¸è¡å åç± col éç½®ç»åååºå¼è§åå³å® */
  @Input()
  @InputNumber()
  col: number;

  @Input()
  default: boolean;

  //#endregion

  constructor(el: ElementRef, private ren: Renderer2, cog: SVConfig) {
    this.el = el.nativeElement;
    Object.assign(this, cog);
  }

  private setClass() {
    const { el, ren, size, layout } = this;
    updateHostClass(el, ren, {
      [`${prefixCls}__container`]: true,
      [`${prefixCls}__${size}`]: true,
      [`${prefixCls}__${layout}`]: true,
      [`clearfix`]: true,
    });
  }

  ngOnInit() {
    this.setClass();
  }

  ngOnChanges() {
    this.setClass();
  }
}
